import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'temp'
})
export class TemperaturPipe implements PipeTransform {

    transform(value: number | string): any {
      if (value === undefined || value === null) { return '-'; }
      if (typeof value === 'string') { value = parseInt(value); }
      if (value > 1100 || value < 0) { return '-'; }

      let intp = Math.floor(Math.abs(value / 10)) * ((value < 0) ? -1 : 1);
      let decp = Math.round((Math.abs(value) - Math.abs(intp) * 10));
      return `${intp},${decp}`;
    }
}
