import { Protocol } from './protocol';
import { LogFunction } from './types';


///////////////
// Log Class //
///////////////

export class Logger {
  debug: boolean;
  type: string;
  logger: LogFunction;

  constructor(type: string, debug: boolean = false) {
    this.logger = (debug) ? console.log : function() {};
    Protocol.setLogger(this.logger);

    this.debug = debug || false;
    this.type = type || 'base';
  }

  info(msg: string) {
    if (this.debug) {
      this.logger(this.type + ' - ' + msg);
    }
  }
  log(msg: string) {
    this.logger(this.type + ' - ' + msg);
  }
  err(msg: string) {
    this.logger(this.type + ' - *** ' + msg + ' ***');
  }

}
