import { Component } from '@angular/core';
import { Unit, UnitMotorCmd, UnitState } from '../system/protocol';

@Component({
  selector: 'updown',
  inputs: ['service'],
  styleUrls: ['updown.scss'],
  template: `
    <ion-item lines="none" class="updown">
      <ion-label>{{service.getName()}}
        <span class="state">{{state()}}</span>
      </ion-label>

      <ion-buttons slot="end">
        <ion-button size="small" slot="icon-only" (click)="doDown(service)">
          <ion-icon name="caret-down" [ngClass]="colorDown()"></ion-icon></ion-button>
        <ion-button size="small" slot="icon-only" (click)="doStop(service)">
          <ion-icon name="stop"></ion-icon></ion-button>
        <ion-button size="small" slot="icon-only" (click)="doUp(service)" [ngClass]="colorUp()">
          <ion-icon name="caret-up" [ngClass]="colorUp()"></ion-icon></ion-button>
      </ion-buttons>
    </ion-item>
  `})

  export class UpDownControl {
    protected service: Unit;

    state() {
      return this.service.getDispayState();
    }
    stateColor() {
      if ((this.service.status === UnitState.kOpen) || (this.service.status === UnitState.kClosed)) {
        return 'done';
      }
      else if ((this.service.status === UnitState.kOpening) || (this.service.status === UnitState.kClosing)) {
        return 'active';
      }
      else {
        return '';
      }
    }

    colorUp() {
      if (this.service.status === UnitState.kOpen) {
        return 'done';
      }
      else if (this.service.status === UnitState.kOpening) {
        return 'active';
      }
      else {
        return '';
      }
    }
    doUp() {
      this.service.setState(UnitMotorCmd.kOpen);
    }

    colorDown() {
      if (this.service.status === UnitState.kClosed) {
        return 'done';
      }
      else if (this.service.status === UnitState.kClosing) {
        return 'active';
      }
      else {
        return '';
      }
    }
    doDown() {
      this.service.setState(UnitMotorCmd.kClose);
    }

    doStop() {
      this.service.setState(UnitMotorCmd.kStop);
    }

  }
