import { Pipe, PipeTransform } from '@angular/core';
import { System } from '../system/system';
import { translations } from './translations';

@Pipe({
  name: '_', pure: false
})
export class TranslatePipe implements PipeTransform {

  constructor(protected system: System) {
  }

  transform(value: string): any {
    return Translate(value, this.system);
  }
}

export function Translate(value: string, system: System) {
  if (!value) { return '[** empty **]'; }

  const lan = (system && system.config && system.config.language) ?
              system.config.language : 'EN';

  if (lan === 'XX') {
    return '[' + value + ']';
  }

  if (typeof translations[lan] === 'undefined') {
    return '[** no-' + lan + ': ' + value + ' **]';
  } 

  const tran = translations[lan][value];
  if (typeof tran === 'undefined') {
    return '[** no-trn: ' + value + ' **]';
  }

  return tran;
}
