import { Component } from '@angular/core';
import { Unit } from '../system/protocol';

@Component({
  selector: 'switch',
  inputs: ['service'],
  styleUrls: ['switch.scss'],
  template: `
    <ion-item lines="none">
      <ion-label tappable (click)="toggle(service)">
        {{service.getName()}}
        <ion-icon class="state" *ngIf="service.status === 2" name="time" color="primary"></ion-icon>
      </ion-label>

      <ion-toggle [(ngModel)]="service.status" 
                  (ionBlur)="change(service)"></ion-toggle>
    </ion-item>
  `})

export class SwitchControl {
  protected service: Unit;

  change(unit: Unit) {
    if (this.service.status == 2) {
    } else {
      unit.setState(unit.status);
    }
  }
  toggle(unit: Unit) {
    unit.setState(!unit.status);
  }

}
