import { Component } from '@angular/core';
import { Unit } from '../system/protocol';

@Component({
  selector: 'temperature',
  inputs: ['service'],
  styleUrls: ['temperature.scss'],
  template: `
    <div class="temp">
      <div class="temp__data">
        <div class="temp__name">
          {{service.getName()}}
          <ion-icon *ngIf="service.preset >= 0" class="right" [color]="colorTemp()" name="thermometer"></ion-icon>
          <!--span class="right">{{statusTemp()}}</span -->
        </div>
        <div class="temp__current">{{service.value | temp}}<span class="temp__celcius">C</span></div>
        <div class="temp__asking">{{ "Temperature.Asking" |_ }}: {{ getTemp() | temp}}<span class="temp__celcius">C</span></div>
      </div>

      <ion-buttons>
        <ion-button fill="solid" slot="icon-only" (click)="incDecTemp(true)">
          <ion-icon name="chevron-up"></ion-icon></ion-button>
        <ion-button fill="solid" slot="icon-only" (click)="incDecTemp(false)">
          <ion-icon name="chevron-down"></ion-icon></ion-button>
      </ion-buttons>
    </div>

    <div class="presets">
      <ion-buttons class="icons">
        <ion-button fill="solid" slot="icon-only" [ngClass]="colorPreset(-1)" (click)="changePreset(-1)">
          <ion-icon name="close-outline"></ion-icon></ion-button>
        <ion-button fill="solid" slot="icon-only" [ngClass]="colorPreset(1)" (click)="changePreset(1)">
          <ion-icon src="/assets/icon/hsun.svg"></ion-icon></ion-button>
        <ion-button fill="solid" slot="icon-only" [ngClass]="colorPreset(0)" (click)="changePreset(0)">
          <ion-icon name="sunny"></ion-icon></ion-button>
        <ion-button fill="solid" slot="icon-only" [ngClass]="colorPreset(3)" (click)="changePreset(3)">
          <ion-icon src="/assets/icon/hmoon.svg"></ion-icon></ion-button>
        <ion-button fill="solid" slot="icon-only" [ngClass]="colorPreset(2)" (click)="changePreset(2)">
          <ion-icon name="moon-sharp"></ion-icon></ion-button>
      </ion-buttons>

      <ion-buttons class="icontext">
        <ion-button fill="clear" mode="ios" (click)="changePreset(-1)" color="dark">
          {{ "Temperature.Off" |_ }}</ion-button>
        <ion-button fill="clear" mode="ios" (click)="changePreset(1)" color="dark">
          {{service.hsun | temp}}<span class="temp__celcius">C</span></ion-button>
        <ion-button fill="clear" mode="ios" (click)="changePreset(0)" color="dark">
          {{service.sun | temp}}<span class="temp__celcius">C</span></ion-button>
        <ion-button fill="clear" mode="ios" (click)="changePreset(3)" color="dark">
          {{service.hmoon | temp}}<span class="temp__celcius">C</span></ion-button>
        <ion-button fill="clear" mode="ios" (click)="changePreset(2)" color="dark">
          {{service.moon | temp}}<span class="temp__celcius">C</span></ion-button>
      </ion-buttons>
    </div>
  `})

/*
  unit.status => 0=idle, 1=heating, 2=cooling
  unit.preset => 0=sun, 1=half sun, 2=moon, 3=half moon, -1=off
  unit.sun    => 10x temperature
  unit.hsun   => 10x temperature
  unit.moon   => 10x temperature
  unit.hmoon  => 10x temperature
  unit.value  => 10x temperature
*/

  export class TemperatureControl {
    protected service: Unit;

    colorTemp(): string {
      if (this.service.status == 1) {
        return 'danger';
      }
      else if (this.service.status == 2) {
        return 'cool';
      }
      else {
        return 'light';
      }
    }

    statusTemp(): string {
      if (this.service.status == 1) {
        return 'Heating';
      }
      else if (this.service.status == 2) {
        return 'Cooling';
      }
      else if (this.service.status == 0) {
        return 'Off';
      }
      else {
        return '-';
      }
    }

    colorPreset(preset) {
      return (this.service.preset === preset) ? 'preset-active' : '';
    }

    getTemp(): number {
      switch (this.service.preset) {
        case 0: return this.service.sun;
        case 1: return this.service.hsun;
        case 2: return this.service.moon;
        case 3: return this.service.hmoon;
      }
    }

    incDecTemp(inc: boolean) {
      this.service.doIncDecPreset(inc);
    }
    changeTemp(delta: number) {
      this.service.setPreset(this.service.preset, <number>this.service.value + delta);
    }
    changePreset(preset: number) {
      if (preset == -1) {
        this.service.sensorOnOff(false);

      } else {
        this.service.sensorOnOff(true);
        this.service.preset = preset;
        this.service.temp = this.getTemp();
        this.service.selectPreset(preset);
      }
    }

  }
