import { Component } from '@angular/core';

import { Platform, MenuController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';

import { System } from './system/system';
import { GroupConfig } from './system/types';


@Component({
  selector: 'app-root',
  templateUrl: "app.component.html"
})
export class AppComponent {

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private menuCtrl: MenuController,
    private system: System

  ) {
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
      this.system.setBrowser(this.platform.is('pwa'));
    });
    this.platform.resume.subscribe(() => {
      console.log("resume event")
      this.system.emitter.emit('refresh');
    });
  }

  selected(): number {
    // find the first visible group
    const group = this.system.groups.find(g => g.visible);
    return (group) ? group.id : 0;
  }

  doRadio(event: CustomEvent) {
    const selected = event.detail.value;
    this.system.groups.forEach(g => { g.visible = (g.id == selected)});
    this.system.writeGroups();
    event.preventDefault();

    // close if the menu controller is open
    this.menuCtrl.close();
  }

  doCheck(event: CustomEvent, group: GroupConfig) {
    group.visible = ! group.visible;
    this.system.writeGroups();
    event.preventDefault();
  }

  done() {
    this.menuCtrl.close();
  }

  splitter(e) {
    this.system.isSplitted = e.detail.visible;
  }
}
